import React, { useContext, useEffect, useState } from "react";
import { useNavigate } from 'react-router-dom';
import "./makecontents.css";
import { MyContext } from "../../../routes/homepage2";

import Makebg from "./makecontentsbtn/makebg";
import Maketext from "./makecontentsbtn/maketext";
import Makequiz from "./makecontentsbtn/makequiz_multiple";
import Makebgm from "./makecontentsbtn/makebgm";



function MutilpleQuizContents() {
  const {
    moduleTitles,
    moduleIntros,

    selectedImage,
    setSelectedImage, //배경만들기

    text, 
    setText, //텍스트만들기

    listItems, setListItems, quizItemsState, setQuizItemsState, //퀴즈만들기

    selectAudio, setSelectAudio, //bgm만들기

    setProjectItems, projectItems,
    resetValues, //모든 요소 초기화
  } = useContext(MyContext);
  const navigate = useNavigate();

  const [showError, setShowError] = useState(false); // 에러 메시지 상태 변수 추가
  const [count, setCount] = useState(0); // 글자 수 count
  const [gameTitle, setGameTitle] = useState('');
  const[guidetext]=useState('문제를 보고 여러 보기 중 정답을 고르세요.(최대 30자 이내)')
  const [useImageCnt, setUseImageCnt] = useState(4); //이미지갯수
  const [multipleCnt, setMultipleCnt] = useState(2);//보기 수
  const [goalAnswerCnt, setGoalAnswerCnt ] = useState(2);//목표정답수
  const [queTime, setQueTime] = useState(3);//문제별시간
  const [isActive, setIsActive] = useState(true);
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleInputChange = (e) => { // 글자입력 변경 핸들러

  const inputLength = e.target.value.length;
    setCount(inputLength); // 글자 수 업데이트
    setGameTitle(e.target.value);
    
  }


  async function getUserName() {
    const url = 'http://13.125.65.215/BS/GetCookie_userID.php';
    
    try {
      const response = await fetch(url);
      if (!response.ok) throw new Error('Network response was not ok');
      const data = await response.text();
      // console.log(`User Name: ${data}`);
      return data;
    } catch (error) {
      throw error;
    }
  }
  
  async function convertToFile(data, filename) {

    // data가 이미 File 객체인 경우, 변환 없이 바로 반환
  if (data instanceof File) {
    return data;
  }

    let response;
    let blob;
    let file;
    let mimeType;
  
    if (data.startsWith('http') || data.startsWith('https')) {
      // URL인 경우
      response = await fetch(data);
      blob = await response.blob();
      mimeType = blob.type;
    } else if (data.startsWith('data:')) {
      // Base64 문자열인 경우
      const base64Data = data.split(',')[1];
      mimeType = data.split(';')[0].split(':')[1];  // MIME 타입 추출
      const byteCharacters = atob(base64Data);
      const byteNumbers = new Array(byteCharacters.length);
      for (let i = 0; i < byteCharacters.length; i++) {
        byteNumbers[i] = byteCharacters.charCodeAt(i);
      }
      const byteArray = new Uint8Array(byteNumbers);
      blob = new Blob([byteArray], { type: mimeType });
    }
  
    if (blob) {
      // MIME 타입을 확장자로 변환
      const extension = mimeType.split('/')[1];
      // 확장자를 파일 이름에 추가
      const fullFilename = `${filename}.${extension}`;
      file = new File([blob], fullFilename, { type: mimeType });  // MIME 타입을 사용하여 File 객체 생성
    }
  
    return file;
  }
  
  async function fetchProjectInfoLength(userName) {
    const url = `http://13.125.65.215/BS/GetProjectInfoLength.php?folderPath=${userName}`;
    const response = await fetch(url);
    const data = await response.text();
    // console.log(url);
    return parseInt(data); // 숫자로 변환하여 반환
  }
  
  async function checkAndCreateProjectInfoFile(userName) {
    let num = await fetchProjectInfoLength(userName);

    const url = `http://13.125.65.215/BS/GetProjectInfo.php?folderPath=${userName}`;
    // console.log(url);
    let existingProjectInfo;
    let response;

    let ip_idx_value; // 초기값 없음
    let bgImageFileName = "";

    let bgm_ip_idx_value; // 초기값 없음
    let bgmFileName = "";

    let bgImageFile;
    let bgmFile;
   


    // folder_path 초기값
    let folderPath = `multiplegameinfo_${num}`
    try {
      response = await fetch(url);
      const data = await response.text();
    if (data) {
      try {
        // 문자열 데이터를 JSON으로 파싱
        // existingProjectInfo= JSON.parse(data);
        // console.log(`Data from project_info.json: ${data}`);
        
        
        if (num<=0) {
          existingProjectInfo.project_info = [];
        }else{
          existingProjectInfo = { "project_info": [] };

          let newProjectData = {
            "p_name": gameTitle,  // gameTitle state 추가
            "folder_path": folderPath,  // postData의 folder_path 값 추가
            "m_type": "multiplechoice"  // 새로운 게임 타입 추가
          }
        
          // 새로운 게임 정보 추가
          existingProjectInfo.project_info.push(newProjectData);
          setProjectItems(prevItems => [...prevItems, newProjectData]);
          // console.log('projectItems: ',projectItems);

        }
      } catch (error) {
        // JSON.parse()에서 오류가 발생하면 새로운 객체를 생성
        // console.log('Error parsing data:', error);
        existingProjectInfo = { "project_info": [] };
      }
    } else {
      // project_info.json 파일이 없을 경우, 새로 생성
      existingProjectInfo = { "project_info": [] };

      let newProjectData = {
        "p_name": gameTitle,  // gameTitle state 추가
        "folder_path": folderPath,  // postData의 folder_path 값 추가
        "m_type": "multiplechoice"  // 새로운 게임 타입 추가
      }
    
      // 새로운 게임 정보 추가
      existingProjectInfo.project_info.push(newProjectData);
      setProjectItems(prevItems => [...prevItems, newProjectData]);
      // console.log('projectItems: ',projectItems);
      
    }


      let files = []; // 파일들을 배열로 저장

      //퀴즈 아이템 이미지 
      let itemFilesPromise = listItems.map(async (item, index) => {
        let file = await convertToFile(item.bgImage, `quizItemImage_${index}`);
        return file;
      });
  //   let itemFilesPromise = listItems.map(async (item, index) => {
  //     let filePromises = [];
    
  //     // 각 아이템의 'bgImage'을 처리
  //     if (item.bgImage) {
  //         let file = await convertToFile(item.bgImage, `quizItemImage_${index}`);
  //         filePromises.push(file);
  //     }
    
  //     return Promise.all(filePromises);
  // });
    
      let itemFiles = await Promise.all(itemFilesPromise);
      files = [...files, ...itemFiles];
    // let itemFiles = await Promise.all(itemFilesPromise);
    // files = [].concat(...itemFiles);

      // 퀴즈 이미지 파일 이름들 추출
      let quizImageNames = itemFiles.map(file => file.name);

      // 퀴즈 답변들 추출
      let quizAnswers = listItems.map(item => item.answer);

      // selectedImage가 base64 문자열인지 확인
      if (selectedImage && typeof selectedImage === "string") {
        if (selectedImage.startsWith("data:image")) {
          ip_idx_value = -1;
          bgImageFile = await convertToFile(selectedImage, 'bgImage'); //배경이미지
          bgImageFileName = bgImageFile.name; // 파일 객체에서 이름 추출
          files.push(bgImageFile); // 배열에 추가
        } else if (selectedImage.startsWith("http")) {
          const urlParts = selectedImage.split("/");
          bgImageFileName = urlParts[urlParts.length - 1]; // URL에서 파일 이름 추출
          // ip_idx_value = parseInt(bgImageFileName.match(/\d+/)[0]); // 파일 이름에서 숫자 추출
          if (selectedImage.includes("/mtc/")) {
            ip_idx_value = 0;
          } else if (selectedImage.includes("/omnom/")) {
            ip_idx_value = 1;
          } else if (selectedImage.includes("/poli/")) {
            ip_idx_value = 2;
          } else if (selectedImage.includes("/pangpang/")) {
            ip_idx_value = 3;
          }
        }
      }


       // selectAudio가 URL인지 파일 객체인지 확인
    if (selectAudio && typeof selectAudio === 'string') {
      if (selectAudio.startsWith('http')) {
        const urlParts = selectAudio.split("/");
        bgmFileName = urlParts[urlParts.length - 1]; // URL에서 파일 이름 추출
        // bgm_ip_idx_value = parseInt(bgmFileName.match(/\d+/)[0]); // 파일 이름에서 숫자 추출
        if (selectAudio.includes("/mtc/")) {
          bgm_ip_idx_value = 0;
        } else if (selectAudio.includes("/omnom/")) {
          bgm_ip_idx_value = 1;
        } else if (selectAudio.includes("/poli/")) {
          bgm_ip_idx_value = 2;
        } else if (selectAudio.includes("/pangpang/")) {
          bgm_ip_idx_value = 3;
        }
      }
    } else if (selectAudio instanceof File) {
      bgm_ip_idx_value = -1;
      bgmFileName = selectAudio.name; // 파일 객체에서 이름 추출
      bgmFile = selectAudio; // 파일 객체 그대로 사용
      files.push(bgmFile); // 배열에 추가
    }

    


      const newModuleInfo ={
        "module_type": "multiplechoice",
        "multiplechoice_bg_info": {
          "ip_idx": ip_idx_value,
          "bg": bgImageFileName
        },
        "multiplechoice_bgm_info": {
          "ip_idx": bgm_ip_idx_value,
          "bgm": bgmFileName
        },
        "guide_text": text,
        "target_answer_count": goalAnswerCnt,
        "button_count": multipleCnt,
        "quiz_time": queTime,
        "quiz_image_list": quizImageNames,
        "quiz_answer_list": quizAnswers
      }

      


      // moduleinfo.json 파일 생성
      const moduleInfoFile = new File([JSON.stringify(newModuleInfo)], "moduleinfo.json", {type: "application/json"});
      
      // 파일 배열에 moduleinfo.json 파일 추가
      files.push(moduleInfoFile);
  
      const postData = new FormData();  // FormData 객체 생성
      postData.append('userID', userName);
      postData.append('folder_path', folderPath);
      postData.append('projectInfo', JSON.stringify(existingProjectInfo));

 
  
      files.forEach((file, index) => {
        postData.append(`file[${index}]`, file); // 각 파일을 'file[index]' key에 추가
      });
  
      const postUrl = 'http://13.125.65.215/BS/UploadFiles.php';
      const postResponse = await fetch(postUrl, {
        method: 'POST',
        body: postData,  // FormData 객체를 body로 설정
      });
      const createData = await postResponse.text();

      
      // console.log(`Created Project Info: ${createData}`);
      return createData;  // 객체를 반환
    } catch (error) {
      console.error('Error:', error);
      throw error;
    }
  }
   
  const handleDoneClick = async () => {  // async 키워드 추가
    if (isSubmitting) return; // 제출 중이면 더 이상 진행하지 않음

    if (gameTitle && gameTitle.length <= 20) {
        try {
          setIsSubmitting(true); // 제출 시작
            const userName = await getUserName();  // await 키워드 사용
            const projectInfo = await checkAndCreateProjectInfoFile(userName);  // await 키워드 사용
            setGameTitle(''); // 입력한 내용을 지움
            setIsActive(false); // 버튼에서 active 클래스를 제거
              
        } catch (error) {
            console.error(error);
        } finally {
          setIsSubmitting(false); // 제출 완료를 표시합니다
        }
    }
    navigate('/logged/make_done');
}
  
  useEffect(() => {
    if (count > 20) {
        setShowError(true);
        const timer = setTimeout(() => {
            setShowError(false);
        }, 3000);
        return () => clearTimeout(timer);
    } else {
        setShowError(false);
    }
}, [count]); // count가 변경될 때마다 실행
  

  useEffect(() => {
    // 컴포넌트가 언마운트될 때 호출
    return () => {
      resetValues(); //모든 모듈 페이지 요소 삭제
    }
  }, []);

  useEffect(() => {
    const handleBeforeUnload = (e) => {
      if (selectedImage !== null || 
        text!=='' || 
        listItems.length > 0 ||
         quizItemsState.length > 0 ||
         selectAudio !==null ) { //★★★★★★★★★★★★★
        e.preventDefault();
        e.returnValue = '';
      }
    };
  
    window.addEventListener('beforeunload', handleBeforeUnload);
  
    return () => {
      
      window.removeEventListener('beforeunload', handleBeforeUnload);
      
    };
  }, [text,selectedImage,listItems.length,quizItemsState.length,selectAudio]); // selectedImage,text가 변경될 때마다 이 effect를 재실행합니다.

  // useEffect(() => {
  //   console.log('selectedImage:', selectedImage !== null);
    
  //   console.log(selectedImage);
  //   console.log('text:', text !== '');
  //   console.log('listItems:', listItems.length > 0);
  //   console.log(listItems);
    
  //   console.log('selectAudio:', selectAudio !== null);
  //   console.log(selectAudio);
  // }, [selectedImage, text, listItems, quizItemsState, selectAudio]);


  return (
    <>
      <div className="make_header_wrap">
        <div className="make_header">
          <div className="make_title">
            <p>{moduleTitles[8]}</p>
          </div>
          <div className="make_intro">
            <p>{moduleIntros[8]}</p>
          </div>
        </div>

        <div className="make_contents">
          <div className="make_contents_item">
            <Makebg
              selectedImage={selectedImage}
              setSelectedImage={setSelectedImage}
              moduleTitle={moduleTitles[8]}
            />
          </div>

          <div className="make_contents_item">
            <Maketext 
            text={text}
            setText={setText}
            moduleTitle={moduleTitles[8]}
            guidetext={guidetext}/>
          </div>

          <div className="make_contents_item">
          <Makequiz
            listItems={listItems}
            setListItems={setListItems}
            quizItemsState={quizItemsState}
            setQuizItemsState={setQuizItemsState}
            moduleTitle={moduleTitles[8]}
            
            useImageCnt={useImageCnt} setUseImageCnt={setUseImageCnt} 
            multipleCnt={multipleCnt} setMultipleCnt={setMultipleCnt} 
            goalAnswerCnt={goalAnswerCnt} setGoalAnswerCnt={setGoalAnswerCnt} 
            queTime={queTime} setQueTime={setQueTime}
            />
          </div>

          

          <div className="make_contents_item">
            <Makebgm 
            selectAudio={selectAudio} 
            setSelectAudio={setSelectAudio}
            moduleTitle={moduleTitles[8]} />
          </div> 
        </div>
        {selectedImage !== null && 
        text !== '' && 
        listItems.length > 0 &&
        selectAudio !== null && (
          <>

            <div className="makecontents_done_wrap">
            <div className="makecontents_done_input">
              <input 
              type="text" 
              className="makecontents_done_title" 
              placeholder="퀴즈의 제목을 입력해 주세요.(20자 이내)" 
              onChange={handleInputChange}
              maxLength={20}></input>
              <div 
              className={`makecontents_done_btn ${gameTitle && gameTitle.length <=20 ? 'active' : ''}`}
              onClick={!isSubmitting && gameTitle && gameTitle.length <=20 ? handleDoneClick : undefined}
              >
                <p>등록완료</p>
              </div>
            </div>
            {showError &&(
              <>
              <p className="title_error_overcount">퀴즈 제목의 글자 수를 초과하였습니다</p>
              </>
            )}
            <p className="makecontents_done_caution">주의사항</p>
            <ul>
              <li>등록완료된 모듈은 스튜디오에서 확인 가능합니다.</li>
              <li>등록이 완료된 모듈은 수정이 불가능 합니다. 신중하게 등록해 주세요.</li>
            </ul>
              
            </div>
            
          </>
          )}
          
      </div>
      

    </>
  );
}

export default MutilpleQuizContents;
