import React from 'react';
import { Routes, Route, Link } from 'react-router-dom';

const SignupSuccess = ()=>{

    return(
        <div className='bg_signup_form'>
        
            <div className='bg_signup_header' style={{marginBottom:131+'px'}}>
                <p>회원가입</p>
            </div>
            <div className='category_line' style={{width:460+'px', marginBottom:101+'px'}}></div>
            <div className='big_blue_font' style={{marginBottom:29+'px'}}>회원가입이<br/>완료되었습니다!</div>
            <div className='black_font' style={{marginBottom:140+'px'}}>비버블록의 콘텐츠를 이용해 보세요.</div>
            <div className='blue_btn'>   
                    <Link to='/login' className='signup_btn'>LOGIN</Link>
            </div>

            <div className='bg_login_footer' style={{marginTop: 40+'px'}} >
                <p>ⓒ BeaverBlock. ALL RIGHTS RESERVED.</p>
            </div>
        </div>

        

    )

}

export default SignupSuccess;