import React, { useState, useContext,useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {MyContext} from '../../../routes/homepage2';
import Carousel from './carousel';


import {ReactComponent as Downarrow} from '../../../img/homepage/downarrow.svg';
import {ReactComponent as Plus} from '../../../img/homepage/plus.svg';


// 이미지 URL들의 배열
const images = [
  'http://13.125.65.215/BS/ModuleInfoImg/m_info_0.png',
  'http://13.125.65.215/BS/ModuleInfoImg/m_info_1.png',
  'http://13.125.65.215/BS/ModuleInfoImg/m_info_2.png',
  'http://13.125.65.215/BS/ModuleInfoImg/m_info_3.png',
  'http://13.125.65.215/BS/ModuleInfoImg/m_info_4.png',
  'http://13.125.65.215/BS/ModuleInfoImg/m_info_5.png',
  'http://13.125.65.215/BS/ModuleInfoImg/m_info_6.png',
  'http://13.125.65.215/BS/ModuleInfoImg/m_info_7.png',
  'http://13.125.65.215/BS/ModuleInfoImg/m_info_8.png',
  'http://13.125.65.215/BS/ModuleInfoImg/m_info_9.png',
];
const moduleTitles = [
  'OX 퀴즈',
  '참, 거짓퀴즈',
  '메모리 퀴즈',
  '직소퍼즐',
  '선잇기',
  '글자퍼즐',
  '순서맞추기',
  '사칙연산',
  '다지선다',
  '워드서치',
  
]

const moduleIntros = [
  '숫자. 텍스트 퀴즈, 계산, 시간 , 이미지매칭 OX퀴즈를 만들 수 있습니다.',
  '텍스트의 참, 거짓을 맞추는 퀴즈를 만들 수 있습니다.',
  '연관있는 이미지, 텍스트를 첨부하여 기억력에 좋은 메모리 퀴즈를 만들 수 있습니다.', 
  '내가 원하는 사진을 간단하게 직소퍼즐로 만들 수 있습니다.',
  '다양한 이미지와 텍스트를 추가하여 선잇기 퀴즈를 만들 수 있습니다.',
  '이미지를 인지하고 맞는 글자를 조합하여 퀴즈를 만들 수 있습니다.',
  '이미지와 텍스트로 내용의 흐름을 이해하고 순서를 맞출 수 있는 퀴즈를 만들 수 있습니다.',
  '문제 출제 범위 입력만으로 다양한 사칙연산 퀴즈를 만들 수 있습니다.',
  '여러 보기중에서 이미지와 매칭되는 정답을 찾는 퀴즈를 만들 수 있습니다.',
  '단어의 입력만으로 여러글자 속에서 단어를 찾는 워드서치를 만들수 있습니다.',
]
function Slide() {

  const navigate = useNavigate();
  
  
  const [selectedSlide, setSelectedSlide] = useState(1);
  const [current, setCurrent] = useState(1);
  const [isDivVisible, setDivVisible] = useState(false);
  const [isArrowVisible, setArrowVisible] = useState(true);
  const [moduleTitle, setModuleTitle] = useState(moduleTitles[0]);
  const [moduleIntro, setModuleIntro]=useState(moduleIntros[0])
  
  const toggleDiv = ()=>{
    setDivVisible(!isDivVisible);
    setArrowVisible(!isArrowVisible);
  }

  const handleSelectSlide = (newSlide) => {
    setSelectedSlide(newSlide);
  };

   // 뒤로가기 버튼을 처리하기 위해 useEffect를 사용합니다.
   useEffect(() => {
    // 이 부분은 브라우저에서 뒤로 가기를 눌렀을 때 실행됩니다.
    // 저장된 전역 상태를 기반으로 현재 선택된 슬라이드를 설정합니다.
    handleSelectSlide(selectedSlide);
  }, []);

  useEffect(() => {
    // selectedSlide가 변경될 때마다 moduleTitle과 moduleIntro를 업데이트합니다.
    setModuleTitle(moduleTitles[selectedSlide - 1]);
    setModuleIntro(moduleIntros[selectedSlide - 1]);
  }, [selectedSlide, moduleTitles, moduleIntros]);

  useEffect(() => {
    setCurrent(selectedSlide);
  }, [selectedSlide]);

  return (
    <div>

      <div className='module_header'>

        <div className='module_header_left'>
          <div><p className='module_title'>{moduleTitle}</p></div>
          <div><p className='module_second_title'>{moduleIntro}</p></div>   
        </div>

        <div className='module_header_right'>
        
          <div className='module_select_btn' onClick={toggleDiv}>
            <p className='module_select_font'>콘텐츠 모듈 선택</p>
            <div style={{marginLeft:16+'px'}}><Downarrow/></div>
            <div className='module_select_wrap'
          onClick={toggleDiv} 
          style={{display: isDivVisible ? 'block' : 'none'}}>
            <div className='module_select' onClick={(e) => e.stopPropagation()}>
            <div onClick={() => {
                setSelectedSlide(1);
                setCurrent(1);
                // setModuleTitle(moduleTitles[0]);
                // setModuleIntro(moduleIntros[0]);
                toggleDiv();
              }}>
                <p className='module_select_font'>{moduleTitles[0]}</p>
              </div>

              <div onClick={() => {
                setSelectedSlide(2);
                setCurrent(2);
                // setModuleTitle(moduleTitles[1]);
                // setModuleIntro(moduleIntros[1]);
                toggleDiv();
              }}>
                <p className='module_select_font'>{moduleTitles[1]}</p>
              </div>

              <div onClick={() => {
                setSelectedSlide(3);
                setCurrent(3);
                // setModuleTitle(moduleTitles[2]);
                // setModuleIntro(moduleIntros[2]);
                toggleDiv();
              }}>
                <p className='module_select_font'>{moduleTitles[2]}</p>
              </div>
              <div onClick={() => {
                setSelectedSlide(4);
                setCurrent(4);
                // setModuleTitle(moduleTitles[3]);
                // setModuleIntro(moduleIntros[3]);
                toggleDiv();
              }}>
                <p className='module_select_font'>{moduleTitles[3]}</p>
              </div>
              <div onClick={() => {
                setSelectedSlide(5);
                setCurrent(5);
                // setModuleTitle(moduleTitles[4]);
                // setModuleIntro(moduleIntros[4]);
                toggleDiv();
              }}>
                <p className='module_select_font'>{moduleTitles[4]}</p>
              </div>
              <div onClick={() => {
                setSelectedSlide(6);
                setCurrent(6);
                // setModuleTitle(moduleTitles[5]);
                // setModuleIntro(moduleIntros[5]);
                toggleDiv();
              }}>
                <p className='module_select_font'>{moduleTitles[5]}</p>
              </div>
              <div onClick={() => {
                setSelectedSlide(7);
                setCurrent(7);
                // setModuleTitle(moduleTitles[6]);
                // setModuleIntro(moduleIntros[6]);
                toggleDiv();
              }}>
                <p className='module_select_font'>{moduleTitles[6]}</p>
              </div>
              <div onClick={() => {
                setSelectedSlide(8);
                setCurrent(8);
                // setModuleTitle(moduleTitles[7]);
                // setModuleIntro(moduleIntros[7]);
                toggleDiv();
              }}>
                <p className='module_select_font'>{moduleTitles[7]}</p>
              </div>
              <div onClick={() => {
                setSelectedSlide(9);
                setCurrent(9);
                // setModuleTitle(moduleTitles[8]);
                // setModuleIntro(moduleIntros[8]);
                toggleDiv();
              }}>
                <p className='module_select_font'>{moduleTitles[8]}</p>
              </div>
              <div onClick={() => {
                setSelectedSlide(10);
                setCurrent(10);
                // setModuleTitle(moduleTitles[9]);
                // setModuleIntro(moduleIntros[9]);
                toggleDiv();
              }}>
                <p className='module_select_font'>{moduleTitles[9]}</p>
              </div>
              
            </div>
          </div>
          <div 
          className='module_select_bg'
          onClick={toggleDiv} 
          style={{display: isDivVisible ? 'block' : 'none'}}></div>
          </div>
        </div>

      </div>

      <div className='module_contents_wrap'>
        <div className='module_carousel'>
          <Carousel images={images} 
          current={current} setCurrent={setCurrent}
          selectedSlide={selectedSlide} 
          onSelectSlide={handleSelectSlide}
          isArrowVisible={isArrowVisible} 
          setModuleTitle={setModuleTitle} 
          moduleTitles={moduleTitles}
          setModuleIntro={setModuleIntro} 
          moduleIntros={moduleIntros}
          isDivVisible={isDivVisible}/>
        </div>
        
      </div>
      <div className='module_carousel_btn_wrap'>
        <div className='module_carousel_btn' onClick={()=>{navigate('/login')}}>
          <p className='module_carousel_btn_font'>만들기 시작</p>
          <div><Plus/></div>
        </div>
      </div>
      
    </div>
  );
}

export default Slide;
