import React, { useState } from 'react';
import {ReactComponent as Logo} from '../../img/loginpage/logo.svg';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';



const LoginForm = ()=>{
     

    const navigate = useNavigate();

    const [formData, setFormData] = useState({
      email: '',
      password: '',
    });
  
    const [errorMessages, setErrorMessages] = useState({
      emailError: '',
      passwordError: '',
      loginError: '',
    });
  
    const handleChange = (e) => {
      const { name, value } = e.target;
      setFormData((prevData) => ({
        ...prevData,
        [name]: value,
      }));
  
      // 입력 필드가 변경될 때마다 에러 메시지 초기화
      setErrorMessages({
        emailError: '',
        passwordError: '',
        loginError: '',
        emptyFieldError: '',
      });
    };
  
    const handleSubmit = (e) => {
      e.preventDefault();
  
      // 간단한 유효성 검사를 수행합니다.
      if (!formData.email && !formData.password) {
        setErrorMessages((prevErrors) => ({
          ...prevErrors,
          emptyFieldError: '이메일과 비밀번호를 입력해주세요.',
        }));
        return;
      }

      if (!formData.email) {
        setErrorMessages((prevErrors) => ({
          ...prevErrors,
          emailError: '이메일을 확인해주세요.',
        }));
        return;
      }
  
      if (!formData.password) {
        setErrorMessages((prevErrors) => ({
          ...prevErrors,
          passwordError: '비밀번호를 확인해주세요.',
        }));
        return;
      }
  
      // 로그인 요청을 서버에 보냅니다.
      // axios.post('http://13.125.65.215/BS/login.php', {
      //   id: formData.email,
      //   pw: formData.password,
      // })
      //   .then((response) => {
      //       if (response.data.status === 'success') {
      //           // 로그인이 성공적으로 처리된 경우
      //           // console.log('서버 응답:', response.data);
      //           navigate('/logged');
      //       } else {
      //           // 로그인 실패
      //           // console.log('로그인 실패');
      //           // console.log('서버 응답:', response.data);
      //           setErrorMessages((prevErrors) => ({
      //               ...prevErrors,
      //               loginError: '이메일 또는 비밀번호를 잘못 입력했습니다. 입력하신 내용을 다시 확인해주세요',
      //           }));
      //       }
      //   })
      //   .catch((error) => {
      //       console.error('에러:', error);
      //   });

      //테스트용도로 주석처리
    };


  

    return(
        <div className='bg_login_content'>
          <div className='bg_login_content_logo'>
          <Logo/>
          </div>
          <div className='bar'></div>
          <p className='login_font'>LOGIN</p>
          

          <form onSubmit={handleSubmit}>

            <div className='bg_login_content_input'>
              <label htmlFor='email' className='content_label'>email</label>
              <input 
              type="email" 
              name='email' 
              value={formData.email}
              onChange={handleChange}
              id='email'

              className='content_input' 
              placeholder='이메일을 입력하세요'/>
            </div>

            <div className='bg_login_content_input'>
              <label htmlFor='password' className='content_label'>password</label>
              <input 
              
              type="password"
              name="password"
              value={formData.password}
              onChange={handleChange}
              
              className='content_input' 
              id='password' 
              placeholder='비밀번호를 입력하세요'/>
            </div>

            <div className='err'>
            <p className='err_msg'>{errorMessages.emailError}</p>
            <p className='err_msg'>{errorMessages.passwordError}</p>
            <p className='err_msg'>{errorMessages.loginError}</p>
            <p className='err_msg'>{errorMessages.emptyFieldError}</p>
            </div>

            <button className='blue_btn' type='submit'>LOGIN</button>

            <div className='bg_login_btn'>
              <Link className='blue_font' type='button' style={{marginRight: 30+'px'}}>아이디/비번찾기</Link>
              <Link className='blue_font' to='/login/signup'>회원가입</Link>
            </div>
           
          </form>

          <div className='bg_login_footer' style={{marginTop:110+'px'}}>
            <p>ⓒ BeaverBlock. ALL RIGHTS RESERVED.</p>
          </div>
          

          

         
        </div>
    )
  };

  export default LoginForm;