import React, {useState, useRef, useEffect } from 'react';

import '../makecontents.css';

import {ReactComponent as Bgicon1} from '../../../../img/homepage/makecontents/card1_pic.svg';
import {ReactComponent as Bgicon2} from '../../../../img/homepage/makecontents/card2_pic.svg';
import {ReactComponent as Close} from '../../../../img/homepage/makecontents/close.svg';
import {ReactComponent as Greybg} from '../../../../img/homepage/makecontents/grey_bg.svg';
import {ReactComponent as Warning} from '../../../../img/homepage/makecontents/icon-warning.svg';

function Makebg({moduleTitle,selectedCard, setSelectedCard}){
  
    const [isModalOpen, setModalOpen] = useState(false);
    const [activeTab, setActiveTab] = useState('upload');
    const [activeIcon, setActiveIcon] = useState('mtc');
    const [bgImage, setBgImage] = useState(null); //미리보기 이미지
    const [errorMessage, setErrorMessage] = useState(null);
    const [errorModalOpen, setErrorModalOpen] = useState(false);
    

    const handleClick = () => {
        setModalOpen(!isModalOpen);
    }

    const handleTabClick = (tab) => {
        setActiveTab(tab);
    }
    
    const handleIconClick = (Icon)=>{
        setActiveIcon(Icon);
    }


    const handleImageClick = (e) => {
        // 모든 이미지의 active 상태를 초기화
        const imageElements = document.querySelectorAll('.make_modal_free_bg_contents_img');
        imageElements.forEach(imgDiv => imgDiv.classList.remove('active'));
    
        // 클릭된 이미지만 active 상태로 설정
        const imgDiv = e.currentTarget;
        imgDiv.classList.add('active');
    
        // 선택된 이미지의 src를 상태에 저장
        setSelectedCard(imgDiv.querySelector('img').src);
    }

    const fileInputRef = useRef(null); // 파일 입력 참조 생성

    const handleUploadClick = () => {
        fileInputRef.current.click(); // 파일 입력 클릭 이벤트
    }

    const handleModifyClick = () => {
        if(fileInputRef.current) {
            fileInputRef.current.click(); // 파일 업로드 input 클릭 이벤트 발생
        }
    }


    const handleCloseAndConfirm = () => {
        if (selectedCard) { // 파일이 업로드되었을 때만 모달을 닫음
            setModalOpen(false);
            setBgImage(selectedCard);
        }
    }

    const handleFileUpload = event => {
        const file = event.target.files[0];
        if (file) {  // 파일 선택 창에서 파일이 선택되었을 때만 validateAndPreviewFile 함수 실행
            validateAndPreviewFile(file);
        }
    }

    const validateAndPreviewFile = (file) => {
        if(file && !['image/jpeg', 'image/png'].includes(file.type)) {
            setErrorMessage("업로드할 수 없습니다. .jpg, .png 형식의 이미지를 사용하세요.");
        } else {
            const reader = new FileReader();
            reader.onloadend = () => {
                setSelectedCard(reader.result);
            }
            reader.readAsDataURL(file);
        }
    }

    const handleDrop = (event) => {
        event.preventDefault();
        const file = event.dataTransfer.files[0];
        validateAndPreviewFile(file);
    }

    const handleDragOver = (event) => {
        event.preventDefault();
    }

    const handleCloseClick = () => {
        if(selectedCard) {
            setErrorModalOpen(true);
        } else {
            setModalOpen(false);
        }
    }

    const handleErrorModalClose = () => {
        setErrorModalOpen(false);
        
    }

    const handleModalClose = () => {
        setErrorModalOpen(false);
        setModalOpen(false);
        setSelectedCard(null);
        setBgImage(null)
    }

    // 에러 메시지가 변경되었을 때 실행되는 useEffect
    useEffect(() => {
        if(errorMessage) {
            setTimeout(() => {
                setErrorMessage(null);
            }, 3000);
        }
    }, [errorMessage]);

    return(
        <>
        
        <div className='make_contents_card_wrap' onClick={handleClick}>
            <div className='make_contents_card1'>
                {bgImage ? ( //배경 이미지가 있으면 이미지 렌더링
                    <div className='make_contents_bg1_thumbnail'>
                        <img src={bgImage} alt="배경 이미지" className='bgImage'/> 
                        <div className='make_contents_bg1_thumbnail_circle'> <p>수정</p> </div>
                    </div>
                    
                ) : (
                    <>
                    <Bgicon1 style={{marginRight:0.38+'rem'}}/>
                    <Bgicon2/>
                    </>
                   
                )}
            </div>
            <div className='make_contents_card2'>
                {bgImage ? (
                    <>
                    <div className='make_contents_bgtitle1_wrap'>
                        <p className='make_contents_title1'>카드 만들기</p>
                    </div>   
                    </>
                    
                ):(
                    <>
                    <p className='make_contents_title1'>카드 만들기</p>
                    <p className='make_contents_title2'>카드의 모양에 사용할<br/>이미지를 첨부해 주세요.</p>

                    </>
                    
                )}
                
            </div>
            
        </div>
        {isModalOpen && (
                <>
                <div className='overlay'></div>
                <div className='make_modal'>
                    <div className='make_modal_header'>
                        <p>{moduleTitle}</p>
                        <div className='make_modal_header_close' onClick={handleCloseClick}>
                            <Close/>
                        </div>
                    </div>
                    <div className='make_modal_content_wrap'>
                        <div className='make_modal_nev'>
                            <div className={activeTab === 'upload' ? 'make_modal_nev_li active' : 'make_modal_nev_li'} onClick={() => handleTabClick('upload')}>
                                <p>카드 이미지 업로드</p>
                            </div>
                            <div  className={activeTab === 'free' ? 'make_modal_nev_li active' : 'make_modal_nev_li'} onClick={() => handleTabClick('free')}>
                                <p>무료 카드 이미지</p>
                            </div>
                        </div>
                        <div className='make_modal_content'>
                            {activeTab === 'upload' && (
                                <div className='make_modal_bg_upload_wrap'  onDrop={handleDrop} onDragOver={handleDragOver}>
                                    {errorMessage && <div className="error_wrong-image"><p>업로드할 수 없습니다. .jpg, .png 형식의 이미지를 사용하세요.</p></div>} 
                                    {/* 에러메세지출력  */}
                                    {selectedCard ? ( //선택된 이미지가 있으면 이미지 미리보기 렌더링
                                        <>
                                        <div className='make_modal_bg_upload_img'>
                                            <img src={selectedCard} alt="선택된 배경" />
                                        </div>
                                        <input type="file" ref={fileInputRef} style={{display: 'none'}} onChange={handleFileUpload} />
                                        <div className='make_modal_bg_modify_btn' onClick={handleModifyClick}>수정하기</div>
                                        </>
                                        
                                         
                                    ):( // 선택된 이미지가 없다면 업로드 안내 및 버튼 렌더링

                                        <div className='make_modal_bg_upload'>
                                            
                                            <div><Greybg/></div>
                                            <p>여기에 파일을 업로드 하세요!<br/>(png, jpg형식 지원)</p>
                                            <input type="file" ref={fileInputRef} style={{display: 'none'}} onChange={handleFileUpload} />
                                            <div className='make_modal_bg_upload_btn' onClick={handleUploadClick}>이미지 첨부하기</div>
                                        </div>

                                    )}
                                    
                                </div>
                                
                            )}
                            {activeTab === 'free' && (
                                <div className='make_modal_free_bg'>
                                    <div className='make_modal_free_bg_icons'>
                                        <div className={activeIcon === 'mtc' ? 'make_modal_free_bg_icon active' : 'make_modal_free_bg_icon' } onClick={() => handleIconClick('mtc')}>
                                            <img src="http://13.125.65.215/BS/CommonRes/icon/common_icon_mtc.png" alt=""/>
                                        </div>
                                        <div className={activeIcon === 'omnom' ? 'make_modal_free_bg_icon active' : 'make_modal_free_bg_icon' } onClick={() => handleIconClick('omnom')}>
                                            <img src="http://13.125.65.215/BS/CommonRes/icon/common_icon_omnom.png" alt=""/>
                                        </div>
                                        <div className={activeIcon === 'poli' ? 'make_modal_free_bg_icon active' : 'make_modal_free_bg_icon' } onClick={() => handleIconClick('poli')}>
                                            <img src="http://13.125.65.215/BS/CommonRes/icon/common_icon_poli.png" alt=""/>
                                        </div>
                                        <div className={activeIcon === 'ppd' ? 'make_modal_free_bg_icon active' : 'make_modal_free_bg_icon' } onClick={() => handleIconClick('ppd')}>
                                            <img src="http://13.125.65.215/BS/CommonRes/icon/common_icon_ppd.png" alt=""/>
                                        </div>
                                    </div>
                                    <div className='make_modal_free_bg_contents_wrap'>
                                         <div className='make_modal_free_bg_contents'>
                                            {activeIcon === 'mtc' &&(
                                                <>
                                                <div className='make_modal_free_bg_contents_img card_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/mtc/common_mcard_mtc_0.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img card_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/mtc/common_mcard_mtc_1.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img card_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/mtc/common_mcard_mtc_2.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img card_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/mtc/common_mcard_mtc_3.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img card_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/mtc/common_mcard_mtc_4.png" alt=""/>
                                                </div>
                                                </>
                                            )}
                                            {activeIcon === 'omnom' &&(
                                                <>
                                                <div className='make_modal_free_bg_contents_img card_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/omnom/common_mcard_omnom_0.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img card_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/omnom/common_mcard_omnom_1.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img card_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/omnom/common_mcard_omnom_2.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img card_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/omnom/common_mcard_omnom_3.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img card_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/omnom/common_mcard_omnom_4.png" alt=""/>
                                                </div>
                                                </>
                                            )}
                                            {activeIcon === 'ppd' &&(
                                                <>
                                                <div className='make_modal_free_bg_contents_img card_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/pangpang/common_mcard_ppd_0.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img card_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/pangpang/common_mcard_ppd_1.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img card_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/pangpang/common_mcard_ppd_2.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img card_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/pangpang/common_mcard_ppd_3.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img card_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/pangpang/common_mcard_ppd_4.png" alt=""/>
                                                </div>
                                                </>
                                            )}
                                            {activeIcon === 'poli' &&(
                                                <>
                                                 <div className='make_modal_free_bg_contents_img card_poli_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/poli/common_mcard_poli_0.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img card_poli_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/poli/common_mcard_poli_1.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img card_poli_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/poli/common_mcard_poli_2.png" alt=""/>
                                                </div>
                                                <div className='make_modal_free_bg_contents_img card_poli_img' onClick={handleImageClick}>
                                                    <img src="http://13.125.65.215/BS/CommonRes/memorycard/poli/common_mcard_poli_3.png" alt=""/>
                                                </div>
                                                </>
                                            )}

                                         </div>

                                    </div>
                                </div>
                            )}
                          
                            
                        </div>
                            <div className='make_modal_footer'>
                                <div className={`make_modal_bg_confirm_btn ${selectedCard ? 'active' : ''}`} onClick={handleCloseAndConfirm}><p>확인</p></div>
                            </div>
                    </div>

                    {errorModalOpen && (

                        <div className='error_modal-close'>
                        <div className='error_modal_header'>
                            <p>경고 메시지</p>
                            <div className='make_modal_header_close' onClick={handleErrorModalClose}>
                                <Close/>
                            </div>
                        </div>
                        <div className='error_modal-close_content_wrap'>
                            <div className='error_modal-close_content'>
                                <Warning/>
                                <p>현재까지 진행한 내용들은 저장되거나 되돌릴 수 없습니다.<br/>정말로 나가시겠습니까?</p>
                                
                            </div>
                            <div className='error_modal_footer'>
                                <div className='make_modal_bg_confirm_btn2-1' onClick={handleErrorModalClose}><p>취소</p></div>
                                <div className='make_modal_bg_confirm_btn2-2 active' onClick={handleModalClose}><p>확인</p></div>

                            </div>   
                        </div>
                        </div>

                    )}

                </div>
                
                </>

            )}
           
            
             
        </>
    )
}

export default Makebg;

