
import React, { useEffect, useState, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import './studiopage.css';
import { MyContext } from "../routes/homepage2";
import {ReactComponent as Choicebtn} from '../img/homepage/choicebtn.svg';
import {ReactComponent as Choicedel} from '../img/homepage/choicedel.svg';
import {ReactComponent as Choicecheck} from '../img/homepage/choicecheck.svg';
import {ReactComponent as Close} from '..//img/homepage/makecontents/close.svg';
import {ReactComponent as Subtract} from '../img/homepage/subtract.svg';
import {ReactComponent as Pageprev} from '../img/homepage/studio_prev.svg';
import {ReactComponent as Pagenext} from '../img/homepage/studio_next.svg';
import {ReactComponent as Warning} from '../img/homepage/makecontents/icon-warning.svg';





const Studio = () => {
  const navigate = useNavigate();
  const [projects, setProjects] = useState([]);
  const [userName, setUserName] = useState('');  // userName 상태 추가
  const { projectItems } = useContext(MyContext);

  const [choiceMode, setChoiceMode] = useState(false);
  const [isChecked, setIsChecked] = useState(Array(projects.length).fill(false));
  const checkedCount = isChecked.filter(item => item).length;
  const [modalSwitch, setModalSwitch] = useState(false);
  const [modalRemove, setModelRemove] = useState(false);

  const [activeButton, setActiveButton] = useState(1);

  const itemsPerPage = 12;
  const startIndex = (activeButton - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;


  const handleButtonClick = (pageNumber) => {
    setActiveButton(pageNumber);
  };

  const handlePrevClick = () => {
    if (activeButton > 1) {
      setActiveButton(activeButton - 1);
    }
  };
  
  const handleNextClick = () => {
    if (activeButton < 4) {
      setActiveButton(activeButton + 1);
    }
  };

  const handleChoiceMode = ()=>{
    setIsChecked(isChecked.map(() => false));
    setChoiceMode(!choiceMode);
  }

const handleCheck = (index) => {
  // console.log("체크된 아이템의 인덱스:", index);
  setIsChecked(prevState => {
    const newState = [...prevState];
    newState[index] = !newState[index];
    return newState;
  });
};






const handleModal = ()=>{
  setModalSwitch(true);
}
const modalCloseClick = ()=>{
  return setModalSwitch(false);
}

const handleErrorModalClose = () => {
  setModelRemove(false);
 
  
}

const handleModalClose =()=>{
  setModelRemove(false);
  handleRemoveProjects();

}
async function getUserName() {
  const url = 'http://13.125.65.215/BS/GetCookie_userID.php';
  
  try {
    const response = await fetch(url);
    if (!response.ok) throw new Error('Network response was not ok');
    const data = await response.text();
    // console.log(`User Name: ${data}`);
    return data;
  } catch (error) {
    throw error;
  }
}
async function fetchProjectInfoLength(userName) {
  const url = `http://13.125.65.215/BS/GetProjectInfoLength.php?folderPath=${userName}`;
  const response = await fetch(url);
  const data = await response.text();
  // console.log(url);
  return parseInt(data); // 숫자로 변환하여 반환
}

const handleNewCreation = async () => {
//   const userName = await getUserName();
//   const projectInfoLength = await fetchProjectInfoLength(userName);
//   if(projectInfoLength >= 3) {
//       handleModal();
//   } else {
//     navigate('/logged');
// }
navigate('/logged');
}
 

  useEffect(() => {
    // async function getUserName() {
    //   const url = 'http://13.125.65.215/BS/GetCookie_userID.php';
      
    //   try {
    //     const response = await fetch(url);
    //     if (!response.ok) throw new Error('Network response was not ok');
    //     const data = await response.text();
    //     // console.log(`User Name: ${data}`);
    //     setUserName(data);  // userName 상태 업데이트
    //     return data;
    //   } catch (error) {
    //     throw error;
    //   }
    // }

    async function getProjectInfo(userName) {
      const url = `http://13.125.65.215/BS/BS_Files/${userName}/project_info.json`;
    
      try {
        const response = await fetch(url);
        if (!response.ok) throw new Error('Network response was not ok');
        const data = await response.json();
        // console.log(`Project Info: ${JSON.stringify(data)}`);
        return data.project_info;
      } catch (error) {
        throw error;
      }
    }

    getUserName()
      .then(userName => getProjectInfo(userName))
      
      .then(projectInfo => {
        setProjects(projectInfo);
        setIsChecked(new Array(projectInfo.length).fill(false));  // 프로젝트 개수만큼의 false로 채워진 isChecked 상태 초기화
      })

      .catch(error => console.error('Error:', error));
  }, []);

const handleClick = async (folderPath) => {
    try {
      await fetch(`http://13.125.65.215/BS/SetCookie.php?value=${userName}/${folderPath}`);
      window.open('http://13.125.65.215/BeaverContents/index.html', '_blank');
    } catch (error) {
      console.error('Error:', error);
    }
  };



const handleRemoveProjects = async () => {
  // isChecked 배열에서 체크된 아이템들의 실제 projects 배열에서의 인덱스를 계산
  const indexesToRemove = isChecked.reduce((acc, checked, index) => {
    if (checked) acc.push(index);
    return acc;
  }, []);

  const selectedProjects = projects.filter((_, index) => indexesToRemove.includes(index)).map(item => item.folder_path);


  // 계산된 인덱스를 사용하여 projects 배열에서 아이템 삭제
  const newProjects = projects.filter((_, index) => !indexesToRemove.includes(index));
  // const selectedProjects = newProjects.filter((_, index) => indexesToRemove.includes(index)).map(item => item.folder_path);

  setProjects(newProjects);



  // isChecked 배열 업데이트
  setIsChecked(new Array(newProjects.length).fill(false));


  const postData = new FormData();
  postData.append('userid', userName);
  postData.append('folder_path', selectedProjects.join(',')); // 배열을 쉼표로 구분한 문자열로 변환하여 추가

  try {
    const response = await fetch('http://13.125.65.215/BS/DeleteContents.php', {
      method: 'POST',
      body: postData, // FormData 객체를 body로 설정
    });

    if (!response.ok) throw new Error('Network response was not ok');

    const responseData = await response.text();
    // console.log('Response:', responseData);
  } catch (error) {
    console.error('Error:', error);
  }
};


useEffect(() => {
  setIsChecked(new Array(projects.length).fill(false));
}, [activeButton, projects.length]);

// useEffect(() => {
//   console.log('프로젝트 리스트 배열',projects)
//   console.log('체크된 리스트 배열',isChecked);
// }, [isChecked]);

  return (
    <div className='studio_container'>
      <div className="studio_header_wrap">
        <div className="make_header">
            <div className="make_title">
              <p>스튜디오</p>
            </div>
            <div className="make_intro">
              <p>직접 제작한 모듈 게임들을 관리하실 수 있습니다.</p>
            </div>
        </div>

        {!choiceMode &&(
            <>
            <div className="studio_choice_btn" onClick={handleChoiceMode}>
              <Choicebtn/>
              <p>선택하기</p>  
            </div>
            </>
        )}  
        {choiceMode &&(
           <>
          <div className='studio_choice_btn_wrap'>
            <div className='studio_choice_remove_btn' onClick={()=>{setModelRemove(true);}}>
              <Choicedel/>
              <p>삭제하기</p>  
            </div>
          <div className="studio_choice_cancel_btn" onClick={handleChoiceMode}>
             <Choicebtn/>
             <p>선택해제</p>  
           </div>

          </div>
           
           </>

        )}

      </div>
     
 

      <div className='grid-container'>

        {projects.slice().reverse().slice(startIndex, endIndex).map((item, index)=> {
          const pageIndex = activeButton - 1; // 현재 페이지 번호에서 1을 빼줍니다.
          const startIndex = pageIndex * itemsPerPage; // 페이지 시작 인덱스 계산
          const actualIndex = projects.length - 1 - (startIndex + index); // 역순 배열에서 각 아이템의 실제 인덱스
        return(
        <div className="grid-item" key={index.id}>
          <div 
            className='pre_game_img' 
            onClick={() => 
              {if(!choiceMode){
                handleClick(item.folder_path)
              }else{handleCheck(actualIndex)}}}
            style={{backgroundImage: `url(http://13.125.65.215/BS/CommonRes/studio_thumb/${item.m_type}.png)`}}
          >
            {choiceMode && (
              <>
              <div className={`overlay2 ${isChecked[actualIndex] ? 'visible' : ''}`}></div>
              <div className={`pre_game_img_checkbox ${isChecked[actualIndex] ? 'checked' : ''}`} onClick={(e) => {
                // e.stopPropagation();
                // handleCheck(actualIndex); // 체크박스 선택 시 역순 인덱스 사용
              }}>
                {isChecked[actualIndex] && <Choicecheck />}
              </div>
              </>
            )}
          </div>
          <p>{item.p_name}</p>
        </div>
      )})}
      </div> 

      <div className='paging_btn_number_wrap'>
        
        <div className='paging_btn_prev' style={{marginRight:0.62+'rem'}}
        onClick={() => activeButton > 1 && handleButtonClick(activeButton - 1)}><Pageprev/></div>

        {Array(Math.ceil(projects.length / itemsPerPage)).fill().map((_, index) => (
          <div
          key={index}
            className={`paging_btn ${activeButton === index + 1 ? 'active' : ''}`}
            onClick={() => handleButtonClick(index + 1)}
          >
            <p>{index + 1}</p>
          </div>
        ))} 
        <div className='paging_btn_next' style={{marginLeft:0.62+'rem'}}
        onClick={() => activeButton < Math.ceil(projects.length / itemsPerPage) && handleButtonClick(activeButton + 1)}><Pagenext/></div>

      </div>
      {!choiceMode&&(
        <div className='make_contents_done_btn_wrap'>
          <div className="make_contents_done_btn_newone" style={{marginRight:0}} onClick={handleNewCreation}><p>새로만들기</p></div>
        </div>
      )}
      <div style={{marginBottom:14.69+'rem'}}></div>

      
      

      {modalSwitch &&(
            <>
            <div className='overlay'></div>
            <div className='error_modal-close'>
                <div className='error_modal_header'>
                    <p>안내 메시지</p>
                    <div className='make_modal_header_close' onClick={modalCloseClick}>
                    <Close/>
                    </div>
                </div>
                <div className='error_modal-close_content_wrap'>
                    <div className='notice_modal-close_content'>
                        <Subtract/>
                        <p><span>최대 3개의 무료 게임을 저장</span>하실 수 있으며 새로운<br/> 
                        게임을 제작하시려면 기존 게임 삭제 후 이용해 주세요.<br/>  
                        상품 설명 문의는 <span>help@beaverblock.com</span> 으로 연락 주세요.</p>
                        
                    </div>
                    <div className='notice_modal_footer'>
                        <div className='make_modal_bg_confirm_btn2-2 active' onClick={modalCloseClick} ><p>확인</p></div>
                    </div>   
                </div>
            </div>
            </>
        )}

        {modalRemove && checkedCount > 0 &&(
          <>
          <div className='overlay'></div>
          <div className='error_modal-close'>
          <div className='error_modal_header'>
              <p>경고 메시지</p>
              <div className='make_modal_header_close' onClick={handleErrorModalClose}>
                  <Close/>
              </div>
          </div>
          <div className='error_modal-close_content_wrap'>
              <div className='error_modal-close_content'>
                  <Warning/>
                  <p>선택하신 {checkedCount}개의 모듈을 정말로 삭제하시겠습니까?<br/>삭제된 모듈은 복구가 불가능 합니다.</p>
                  
              </div>
              <div className='error_modal_footer'>
                  <div className='make_modal_bg_confirm_btn2-1' onClick={handleErrorModalClose}><p>취소</p></div>
                  <div className='make_modal_bg_confirm_btn2-2   active' onClick={handleModalClose}><p>확인</p></div>

              </div>   
          </div>
          </div>

          </>
         
        )

        }
    </div>
  );
}

export default Studio;

