import React from 'react';
import './otherpage.css';
import {ReactComponent as Appstore} from '../../../img/homepage/icon_app_store.svg';
import {ReactComponent as Googleplay} from '../../../img/homepage/icon_google_play.svg';
import {ReactComponent as Gotop} from '../../../img/homepage/go_top.svg';


const Product=()=>{
    return(
        <div className='product_wrap'>
            <div className='go_top_logo' onClick={() => window.scrollTo(window.scrollX, 0)}><Gotop/></div>
            <div className='product_header_wrap'>
                <div className='product_header'>
                    <div className='product_header_font1'><p>우리 아이 취향 맞춤 캐릭터가 한가득</p></div>
                    <div className='product_header_font2'><p>수만명이 경험한 글로벌 학습 플랫폼</p></div>
                </div>
            </div>

            <div className='product_product_wrap'>
                <div className='product_product'>
                    <div className='product_font1'><p>PRODUCT</p></div>
                    <div className='product_font2'><p>Best Beaverblock Apps</p></div>
                    <div className='product_bestapps_list_wrap'>
                        <div className='product_bestapps_list'>
                            <div className='product_bestapps_list_img1'></div>
                            <div className='product_bestapps_list_title'>비버스쿨</div>
                            <div className='product_bestapps_list_content'><p>출판, 애니메이션, 장난감, 캐릭터<br/> 산업의 다양한 활동을인터랙티브<br/>
                             콘텐츠로 개발하여 월정액<br/> 서비스로제공합니다.</p></div>
                             <div className='product_bestapps_list_btn_wrap'>
                                <div className='product_bestapps_list_btn'>
                                    <Appstore/>
                                    <p 
                                    style={{marginLeft:0.56+'rem'}}
                                    onClick={() => window.open('https://apps.apple.com/kr/app/%EB%B9%84%EB%B2%84%EC%8A%A4%EC%BF%A8/id1542907925', '_blank')}
                                    >App Store</p>
                                </div>
                                <div className='product_bestapps_list_btn'>
                                    <Googleplay/>
                                    <p
                                    onClick={() => window.open('https://play.google.com/store/apps/details?id=com.BeaverBlock.BabyBeaver&hl=ko&gl=US&pli=1', '_blank')}

                                    >Google Play</p>
                                </div>
                            </div>
                        </div>
                        <div className='product_bestapps_list'>
                            <div className='product_bestapps_list_img2'></div>
                            <div className='product_bestapps_list_title'>로보카폴리랑한글</div>
                            <div className='product_bestapps_list_content' style={{marginBottom:2+'rem'}}><p>인터랙션놀이와 교육을 결합한<br/>유아한글학습 프로그램으로 체험기반<br/>
                             상호작용 인터랙션 콘텐츠를 통해<br/> 한글을 배우고 두뇌를 개발하는<br/>놀이학습 앱 입니다.</p></div>
                            <div className='product_bestapps_list_btn_wrap'>
                                <div className='product_bestapps_list_btn'>
                                    <Appstore/>
                                    <p style={{marginLeft:0.56+'rem'}}
                                    onClick={() => window.open('https://apps.apple.com/kr/app/%EB%A1%9C%EB%B3%B4%EC%B9%B4%ED%8F%B4%EB%A6%AC%EB%9E%91-%ED%95%9C%EA%B8%80/id1587088859', '_blank')}
                                    >App Store</p>
                                </div>
                                <div className='product_bestapps_list_btn'>
                                    <Googleplay/>
                                    <p
                                    onClick={() => window.open('https://play.google.com/store/apps/details?id=com.BeaverBlock.PoliKorean', '_blank')}
                                    >Google Play</p>
                                </div>
                            </div>
                        </div>
                        <div className='product_bestapps_list'>
                            <div className='product_bestapps_list_img3'></div>
                            <div className='product_bestapps_list_title'>로보카폴리랑영어</div>
                            <div className='product_bestapps_list_content'><p>영어를처음 접하는 아이들에게 꼭<br/>필요한 영어 200단어를 다양한<br/>
                             게임 요소와 상호 작용 게임을 통해<br/>쉽게 배울 수 있는 앱 입니다.</p></div>
                             <div className='product_bestapps_list_btn_wrap'>
                                <div className='product_bestapps_list_btn'>
                                    <Appstore/>
                                    <p 
                                    style={{marginLeft:0.56+'rem'}}
                                    onClick={() => window.open('https://apps.apple.com/kr/app/%EB%A1%9C%EB%B3%B4%EC%B9%B4%ED%8F%B4%EB%A6%AC%EB%9E%91-%EC%98%81%EC%96%B4/id6443573335', '_blank')}
                                    >App Store</p>
                                </div>
                                <div className='product_bestapps_list_btn'>
                                    <Googleplay/>
                                    <p
                                    onClick={() => window.open('https://play.google.com/store/apps/details?id=com.BeaverBlock.LearnEnglishwithRobocarPoli', '_blank')}
                                    >Google Play</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className='product_studio_wrap'>
                <div className='product_studio'>
                    <div className='product_font1'><p>Beaver Studio</p></div>
                    <div className='product_font2'><p>비버스튜디오</p></div>
                    <div className='product_font3'><p>비버스튜디오는 누구나 쉽고 다양한<br/> 인터렉션 개발이 가능한 템플릿을 제공합니다.</p></div>

                    <div className='product_font_monitor_wrap'>
                        <div className='product_font_monitor'>
                            <div className='product_font_monitor_img'></div>
                            <p>콘텐츠 제작 페이지</p>                                
                        </div>
                        <p>모듈 선정 페이지</p>
                    </div>
                    <div className='product_module_imgs_wrap'>
                        <div className='product_module_imgs'>
                            <div className='product_module_img1'></div>
                            <div className='product_module_img2'></div>
                            <div className='product_module_img3'></div>
                            <div className='product_module_img4'></div>
                        </div>
                        <div className='product_module_font'><p>10종의 다양한 모듈</p></div>
                    </div>
                    
                </div>
            </div>
        </div>
        
    )
}
export default Product;