import React from 'react';
import { Outlet } from 'react-router-dom';

import hobi from '../img/loginpage/poli_logo.png';



const LoginPage = ()=>{
    return(
        <div className='index_page'>
          <div className='bg_container'>

            <div className='bg_side'>
              
              <div className='bg_side_content' >
                <div className='bg_side_content_font'>
                  <p>Welcome<br/><span>beaver studio!</span></p>
                </div>
                <div className='bg_side_content_img'>
                  <img  src={hobi} alt='hobi'/>
                </div>
                
               
              </div>
            </div>


            <Outlet></Outlet>

          
          </div>

          <div className='bg_footer'>
          <div className='bg_footer_btn'>
          <a href='https://beaverblock.com/pages/termsofservice' target='_blank' className='bg_footer_btn_font' style={{marginRight:54+'px'}}>이용약관</a>
          <a href='https://beaverblock.com/pages/privacypolicy' target='_blank' className='bg_footer_btn_font'>개인정보 처리방침</a>
          </div>
          <div className='bg_footer_address'>
          <p>경기도용인시기흥구중부대로184(힉스유타워) 에이동 1009-2호<br/>
          TEL : 070-4354-0803 사업자등록번호 : 510-87-01472 MAIL: help@beaverblock.com<br/>
          (주) 비버블록</p>
          </div>

          </div>
      </div>
    )
}

export default LoginPage



